.header{
    margin-top: 0;
    background-color: black;
}

.navList{
    list-style-type: none;
    padding: 0;
    display: flex;
    gap: 20px;
    margin-top: 0;
    background-color: black;
    height: 60px;
    padding-top: 20px;
    /*padding-left: 1085px;*/
    margin-left: auto;
    margin-right: 70px;
    width: fit-content;
    
}


.navLink{
    color: white;
    font-size: 28px;
    text-decoration: none;
    font-family: 'Arial', sans-serif;
   
}

.navLink:hover{
   background: linear-gradient(to bottom, #5fffbf, #1b294c);
    -webkit-background-clip: text;
    background-clip: text;
   -webkit-text-fill-color: transparent;
    
}



